body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  font-family: "Helvetica Neue";
  font-size: 2rem;
  font-weight: 500;
  color: #170621;
}

.timer-inner {
  margin: 50px auto;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 700px;
}

.timer-button {
  margin: auto 20px;
  padding: 10px;
  width: 80px;
  height: 80px;
  border: 1px solid rgba(203, 92, 213, 1);
  border-radius: 50%;
  outline: none;
  text-transform: uppercase;
  cursor: pointer;
  background: linear-gradient(
    101deg,
    rgba(248, 245, 249, 1) 0%,
    rgba(213, 186, 229, 1) 42%,
    rgba(203, 92, 213, 1) 100%
  );
}

.timer-progress {
  margin: 30px auto;
  width: 1000px;
  height: 50px;
  border: 1px solid #2bd2ff;
  border-radius: 10px;
  background-color: #fa8bff;
  background-image: linear-gradient(
    45deg,
    #fa8bff 0%,
    #2bd2ff 52%,
    #2bff88 90%
  );
}

.timer-progress > h3 {
  font-size: 1.2rem;
  font-weight: 400;
  text-align: left;
  margin: 10px;

}

